import { useMemo, useState } from "react";
import { Grid, Stack, Typography, Dialog, DialogTitle, DialogContent, Chip, Divider, Box } from "@mui/material";
import { useOutletContext } from "react-router-dom";

import PlayerCard from "../components/cards/PlayerCard";
import LoadingSpinner from "../components/common/LoadingSpinner";
import { usePlayerRiskProfile, useSuspiciousPlayers } from "../hooks/useSecurityData";
import { PlayerProfile } from "../types/security";
import { formatDateTime } from "../utils/formatters";

type LayoutOutletContext = {
  searchTerm: string;
};

const PlayersPage = () => {
  const { searchTerm } = useOutletContext<LayoutOutletContext>();
  const { data: suspiciousPlayers, isLoading } = useSuspiciousPlayers();
  const [selectedPlayer, setSelectedPlayer] = useState<PlayerProfile | null>(null);
  const { data: liveProfile } = usePlayerRiskProfile(selectedPlayer?.id, {
    enabled: Boolean(selectedPlayer)
  });
  const handleInspect = (playerProfile: PlayerProfile) => setSelectedPlayer(playerProfile);
  const handleReport = (playerProfile: PlayerProfile) => setSelectedPlayer(playerProfile);

  const filteredPlayers = useMemo(() => {
    if (!suspiciousPlayers) return [];
    if (!searchTerm) return suspiciousPlayers;
    return suspiciousPlayers.filter((player) =>
      player.username.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [suspiciousPlayers, searchTerm]);

  if (isLoading) {
    return <LoadingSpinner fullscreen message="Chargement des profils joueurs…" />;
  }

  return (
    <Stack spacing={4}>
      <Stack direction="row" justifyContent="space-between" alignItems="center">
        <Box>
          <Typography variant="h4" fontWeight={700}>
            Joueurs suivis
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Recherche et suivi des profils signalés récemment.
          </Typography>
        </Box>
        <Chip label={`${filteredPlayers.length} résultats`} color="primary" />
      </Stack>
      <Grid container spacing={3}>
        {filteredPlayers.map((player) => (
          <Grid item key={player.id} xs={12} md={6} lg={4}>
            <PlayerCard player={player} onInspect={handleInspect} onReport={handleReport} />
          </Grid>
        ))}
      </Grid>
      <Dialog
        open={Boolean(selectedPlayer)}
        onClose={() => setSelectedPlayer(null)}
        maxWidth="md"
        fullWidth
        aria-labelledby="player-dialog-title"
      >
        <DialogTitle id="player-dialog-title">{selectedPlayer?.username}</DialogTitle>
        <DialogContent dividers>
          {liveProfile ? (
            <Stack spacing={3}>
              <Stack direction="row" spacing={2} alignItems="center">
                <Typography variant="body1">
                  Score de risque actuel : <strong>{liveProfile.riskScore.toFixed(0)}</strong>
                </Typography>
                <Chip
                  label={`Probabilité de triche ${liveProfile.cheatProbability.toFixed(0)}%`}
                  color={liveProfile.riskScore > 85 ? "error" : "warning"}
                />
              </Stack>
              <Stack spacing={1}>
                <Typography variant="subtitle2" color="text.secondary">
                  Historique des parties
                </Typography>
                <Stack spacing={1}>
                  {liveProfile.history.map((entry) => (
                    <Stack
                      key={entry.timestamp}
                      direction="row"
                      justifyContent="space-between"
                      divider={<Divider flexItem orientation="vertical" />}
                    >
                      <Typography variant="body2">{formatDateTime(entry.timestamp)}</Typography>
                      <Typography variant="body2">Risque {entry.riskScore.toFixed(0)}</Typography>
                    </Stack>
                  ))}
                </Stack>
              </Stack>
              <Stack spacing={1}>
                <Typography variant="subtitle2" color="text.secondary">
                  Signaux actifs
                </Typography>
                <Stack direction="row" spacing={1} flexWrap="wrap">
                  {liveProfile.flags.map((flag) => (
                    <Chip key={flag} label={flag} variant="outlined" />
                  ))}
                </Stack>
              </Stack>
            </Stack>
          ) : (
            <LoadingSpinner message="Récupération du profil en cours…" />
          )}
        </DialogContent>
      </Dialog>
    </Stack>
  );
};

export default PlayersPage;

