import {
  Avatar,
  Box,
  Card,
  CardActions,
  CardContent,
  Chip,
  LinearProgress,
  Stack,
  Typography,
  Button
} from "@mui/material";
import VisibilityIcon from "@mui/icons-material/Visibility";
import FlagIcon from "@mui/icons-material/Flag";
import { useMemo } from "react";

import { PlayerProfile } from "../../types/security";
import { formatDateTime } from "../../utils/formatters";

type PlayerCardProps = {
  player: PlayerProfile;
  onInspect?: (player: PlayerProfile) => void;
  onReport?: (player: PlayerProfile) => void;
};

const getRiskLabel = (score: number) => {
  if (score >= 85) return { label: "Critique", color: "error" as const };
  if (score >= 70) return { label: "Élevé", color: "warning" as const };
  if (score >= 45) return { label: "Modéré", color: "info" as const };
  return { label: "Faible", color: "success" as const };
};

const PlayerCard = ({ player, onInspect, onReport }: PlayerCardProps) => {
  const riskMeta = useMemo(() => getRiskLabel(player.riskScore), [player.riskScore]);

  return (
    <Card variant="outlined" sx={{ borderRadius: 20, height: "100%" }}>
      <CardContent>
        <Stack spacing={2}>
          <Stack direction="row" spacing={2} alignItems="center">
            <Avatar
              src={`https://api.dicebear.com/7.x/identicon/svg?seed=${player.username}`}
              alt={player.username}
              sx={{ width: 56, height: 56 }}
            />
            <Box>
              <Typography variant="subtitle1" fontWeight={600}>
                {player.username}
              </Typography>
              <Typography variant="caption" color="text.secondary">
                Dernière activité : {formatDateTime(player.lastSession)}
              </Typography>
            </Box>
          </Stack>
          <Stack direction="row" spacing={1} alignItems="center">
            <Typography variant="h5" fontWeight={700}>
              {player.riskScore.toFixed(0)}
            </Typography>
            <Chip color={riskMeta.color} size="small" label={riskMeta.label} />
            <Typography variant="caption" color="text.secondary">
              Probabilité de triche {player.cheatProbability.toFixed(0)}%
            </Typography>
          </Stack>
          <Box>
            <Typography variant="caption" color="text.secondary">
              Évolution du risque
            </Typography>
            <LinearProgress
              variant="determinate"
              value={player.riskScore}
              sx={{ height: 6, borderRadius: 999, mt: 0.5 }}
            />
          </Box>
          <Stack spacing={1}>
            <Typography variant="caption" color="text.secondary">
              Signaux actifs
            </Typography>
            <Stack direction="row" spacing={1} flexWrap="wrap">
              {player.flags.map((flag) => (
                <Chip key={flag} label={flag} size="small" variant="outlined" />
              ))}
            </Stack>
          </Stack>
        </Stack>
      </CardContent>
      <CardActions sx={{ justifyContent: "space-between", px: 3, pb: 3 }}>
        <Button
          variant="contained"
          color="primary"
          size="small"
          startIcon={<VisibilityIcon />}
          onClick={() => onInspect?.(player)}
        >
          Voir le profil
        </Button>
        <Button
          variant="outlined"
          color="warning"
          size="small"
          startIcon={<FlagIcon />}
          onClick={() => onReport?.(player)}
        >
          Signaler
        </Button>
      </CardActions>
    </Card>
  );
};

export default PlayerCard;

